import * as yup from 'yup';

export default {
  radius: yup
    .number()
    .min(0)
    .max(50)
    .meta({
      type: 'range',
      values: [0, 50],
      defaultValue: 0
    }),
  margin: yup
    .number()
    .min(0)
    .max(25)
    .meta({
      type: 'range',
      values: [0, 25],
      defaultValue: 0
    }),
  background: yup
    .string()
    .matches(/^#([0-9a-f]{3}|[0-9a-f]{6}|[0-9a-f]{8})$/i)
    .meta({
      type: 'color',
      defaultValue: '#ffffff'
    }),
  width: yup
    .number()
    .min(1)
    .meta({
      type: 'number',
      values: [0, 2048],
      defaultValue: 0
    }),
  height: yup
    .number()
    .min(1)
    .meta({
      type: 'number',
      values: [0, 2048],
      defaultValue: 0
    })
};
